<template>
  <div class="show_content">
    <div v-for="(n,index) in ex" :key="index">
      {{index+1}}. {{n.name}}
      <v-btn :class="favs[index].fav!=0 ? 'red--text' : ''" icon @click="likeFav(index)">
        <v-icon :dense="true">mdi-heart</v-icon>
      </v-btn>
      <img v-bind:src="n.foodImg" />
      <!--span v-if="n.count!=''">喜愛數:{{n.count}}</span>
      <span v-else>{{info(index)}}</span-->
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      ex: [
        { name: "自訂文字或圖片1", count: "", foodImg:require('../img/food5.png') },
        { name: "自訂文字或圖片2", count: "", foodImg:require('../img/food2.png') },
        { name: "自訂文字或圖片3", count: "", foodImg:require('../img/food6.png') },
        { name: "自訂文字或圖片4", count: "", foodImg:require('../img/food3.png') },
        { name: "自訂文字或圖片5", count: "", foodImg:require('../img/food4.png') },
        { name: "自訂文字或圖片6", count: "", foodImg:require('../img/food1.png') },
      ],
      favs: this.$store.state.menuinfo.favs,
    };
  },
  methods: {
    likeFav(index) {
      //計算喜歡~因不是存取數據庫刷新歸0,無法紀錄
      this.$store.commit("menuinfo/set_fav", index);

      if (this.favs[index].fav == 1) {
        this.updata(index, "+");
      } else {
        this.updata(index, "-");
      }
    },
    updata(index, sign) {
      axios
        .post(
          "http://localhost:8081/likeMenu/menu",
          this.$qs.stringify({ name: this.ex[index].name, sign: sign })
        )
        .then((response) => {
          this.ex[index].count = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    info(index) {
      axios
        .post(
          "http://localhost:8081/likeMenu/menu",
          this.$qs.stringify({ name: this.ex[index].name, sign: "*" })
        )
        .then((response) => {
          this.ex[index].count = response.data;
          //this.ex=response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>